	module.exports = {"data":{"markdownRemark":{"html":"<h2>Array.prototype.forEach</h2>\n<p>The ‘forEach’ array method is used to iterate through each item in an array.  The method is called on the array Object and is passed a function that is called on each item in the array.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">var</span> arr <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">,</span> <span class=\"token number\">2</span><span class=\"token punctuation\">,</span> <span class=\"token number\">3</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">,</span> <span class=\"token number\">5</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\narr<span class=\"token punctuation\">.</span><span class=\"token function\">forEach</span><span class=\"token punctuation\">(</span>number <span class=\"token operator\">=></span> console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>number <span class=\"token operator\">*</span> <span class=\"token number\">2</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token comment\" spellcheck=\"true\">// 2</span>\n<span class=\"token comment\" spellcheck=\"true\">// 4</span>\n<span class=\"token comment\" spellcheck=\"true\">// 6</span>\n<span class=\"token comment\" spellcheck=\"true\">// 8</span>\n<span class=\"token comment\" spellcheck=\"true\">// 10</span>\n</code></pre>\n      </div>\n<p>The callback function can also take a second parameter of an index in case you need to reference the index of the current item in the array.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">var</span> arr <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span><span class=\"token number\">1</span><span class=\"token punctuation\">,</span> <span class=\"token number\">2</span><span class=\"token punctuation\">,</span> <span class=\"token number\">3</span><span class=\"token punctuation\">,</span> <span class=\"token number\">4</span><span class=\"token punctuation\">,</span> <span class=\"token number\">5</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\narr<span class=\"token punctuation\">.</span><span class=\"token function\">forEach</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span>number<span class=\"token punctuation\">,</span> i<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token template-string\"><span class=\"token string\">`</span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>number<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\"> is at index </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>i<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">`</span></span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token comment\" spellcheck=\"true\">// '1 is at index 0'</span>\n<span class=\"token comment\" spellcheck=\"true\">// '2 is at index 1'</span>\n<span class=\"token comment\" spellcheck=\"true\">// '3 is at index 2'</span>\n<span class=\"token comment\" spellcheck=\"true\">// '4 is at index 3'</span>\n<span class=\"token comment\" spellcheck=\"true\">// '5 is at index 4'</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p><a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/forEach' target='_blank' rel='nofollow'>MDN Article on Array.prototype.forEach()</a></p>","fields":{"slug":"/javascript/standard-objects/array/array-prototype-foreach/"},"frontmatter":{"title":"Array.prototype.forEach"}}},"pathContext":{"slug":"/javascript/standard-objects/array/array-prototype-foreach/"}}

